.login-main-content{
    max-width: 1000px;
    margin: 0 auto;
}

.login-title{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
    margin-top: 10%;
}
.login-form{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 5%;
    .login-input{
        width: 70%;
        margin: 0 auto;
        margin-bottom: 5%;
    }

    button{
        width: 70%;
        margin: 0 auto;
        background-color: RGBA(176, 133, 97, 1.00);
        color: white;
        height: 55px;
    }
}

form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 2px solid darkgray;
    padding-top: 5%;
    padding-bottom: 5%;
}